.container {
    width: 1720px;
    max-width: 100%;
    padding: 0 45px;
    margin: 0 auto;
    height: 630px;
    min-height: 100vh;
    position: relative;
    overflow: hidden;
}

.socials-list {
    &__item {
        position: relative;
        vertical-align: middle;

        svg path {
            transition: .3s ease;
        }
    }
    &__icon:hover {
        svg path {
            fill: $accent-color;
        }
    }
    li + li {
        margin-left: 32px;
    }
}

.content {
    display: flex;
    height: 100%;

    &__section {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        //padding-top: 120px;
        max-width: 45%;
    }

    &__title {
        font-size: 72px;
        line-height: 1.18;
        max-width: 848px;
        margin-bottom: 32px;
        margin-top: 0;
    }

    &__subtitle {
        font-size: 18px;
        line-height: 1.77;
        max-width: 720px;
        margin-bottom: 0;
    }
}

.decor {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;

    img {
        max-width: none;
        position: absolute;
        top: 50%;
        right: -245px;
        transform: translateY(-50%);
        //max-height: 120%;
        //margin-top: 0;
        margin-top: 115px;
        max-height: 1410px;
    }
}

.letter {
    display: inline-block;
    min-width: 20px;
}
